  box-sizing: border-box;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

h1 {
  color: #fcdc2a;
  font-family: "Dancing Script", cursive;
}

a {
  color: #fcdc2a;
}

a:hover,
a:active {
  color: #87a922;
}

body {
  width: 100%;
  background-color: #fbfbfb;
  display: flex;
  flex-direction: column;
  font-family: "Poppins", sans-serif;
}

.skip-link {
  position: absolute;
  text-decoration: none;
  top: -40px;
  left: 0;
  background-color: #87a922;
  color: #fbfbfb;
  padding: 8px;
  z-index: 101;
}

.skip-link:hover {
  color: #87a922;
}

.skip-link:focus {
  top: 0;
}

header {
  display: inline;
}

nav {
  position: fixed;
  background-color: #114232;
  display: flex;
  padding: 0 64px;
  height: auto;
  width: 100%;
  justify-content: space-between;
  z-index: 100;
}

nav h1 {
  margin: 16px 0;
  text-transform: uppercase;
  letter-spacing: 1px;
}

nav h1 a {
  text-decoration: none;
  font-size: 2rem;
}

.shadow-bottom {
  box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 7px 0px;
}

.menu-list {
  font-size: 18px;
  margin: 24px 0;
  display: flex;
}

.menu-list li {
  padding: 0 0 0 10px;
  margin-left: 10px;
  list-style-type: none;
  display: inline-block;
}

.menu-list a {
  color: #fcdc2a;
  text-decoration: none;
}

.menu-list a:hover,
.menu-list a:active {
  font-weight: 800;
  transition: ease-out;
  transition-duration: 0.2s;
  border-bottom: 2px solid #fcdc2a;
  color: #fcdc2a;
}

.menu-button {
  display: none;
}

.hero {
  display: flex;
  vertical-align: middle;
  align-items: center;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) ;
  width: 100%;
  height: 100vh;
  background-position: center;
  background-color: #000;
}

.hero img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  z-index: 1;
  opacity: 0.5;
}

.hero .inner {
  text-align: center;
  margin: 0 auto;
  padding: 0 32px;
  z-index: 2;
}

.hero .title {
  color: #fbfbfb;
  font-weight: 600;
  font-size: 3rem;
}

.hero .subtitle {
  color: #fbfbfb;
  margin-top: 10px;
  font-size: 1.8rem;
}

main {
  margin: 32px 32px 0;
}

#content {
  width: 100%;
  margin-top: 1em;
}

#content h2 {
  text-align: center;
  margin-bottom: 1em;
}

.title-content {
  border-bottom: 3px solid #114232;
}

.row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 16px 0;
}

.col {
  width: calc(100% / 3 - 16px);
  margin-bottom: 32px;
  display: flex;
  flex-direction: column;
}

.card {
  background-color: #fbfbfb;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 7px 0px;
  flex: 1;
}

.card .city-label {
  background-color: #c36a2d;
  margin: 1em;
  padding: 0.5em 1em;
  position: absolute;
  color: #fbfbfb;
  font-weight: 600;
  text-align: left;
}

.card img {
  width: 100%;
  max-height: 250px;
  object-fit: cover;
}

.card-body {
  padding: 16px;
}

.card-title a {
  font-size: 2rem;
  font-weight: 600;
  text-decoration: none;
}

.card-text {
  color: black;
}

.card .rating {
  color: #c36a2d;
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 0.5em;
}

footer {
  text-align: center;
  color: #fbfbfb;
  background-color: #114232;
  font-weight: 700;
  padding: 5vh 0;
}

footer a {
  text-decoration: none;
  color: #fbfbfb;
}

footer a:hover,
footer a:active {
  text-decoration: none;
  color: #eadca6;
}`, "",{"version":3,"sources":["webpack://./src/styles/style.css"],"names":[],"mappings":"AAAA;EACE,sBAAsB;EACtB,SAAS;EACT,UAAU;EACV,uBAAuB;AACzB;;AAEA;EACE,cAAc;EACd,sCAAsC;AACxC;;AAEA;EACE,cAAc;AAChB;;AAEA;;EAEE,cAAc;AAChB;;AAEA;EACE,WAAW;EACX,yBAAyB;EACzB,aAAa;EACb,sBAAsB;EACtB,kCAAkC;AACpC;;AAEA;EACE,kBAAkB;EAClB,qBAAqB;EACrB,UAAU;EACV,OAAO;EACP,yBAAyB;EACzB,cAAc;EACd,YAAY;EACZ,YAAY;AACd;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,MAAM;AACR;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,eAAe;EACf,yBAAyB;EACzB,aAAa;EACb,eAAe;EACf,YAAY;EACZ,WAAW;EACX,8BAA8B;EAC9B,YAAY;AACd;;AAEA;EACE,cAAc;EACd,yBAAyB;EACzB,mBAAmB;AACrB;;AAEA;EACE,qBAAqB;EACrB,eAAe;AACjB;;AAEA;EACE,8CAA8C;AAChD;;AAEA;EACE,eAAe;EACf,cAAc;EACd,aAAa;AACf;;AAEA;EACE,mBAAmB;EACnB,iBAAiB;EACjB,qBAAqB;EACrB,qBAAqB;AACvB;;AAEA;EACE,cAAc;EACd,qBAAqB;AACvB;;AAEA;;EAEE,gBAAgB;EAChB,oBAAoB;EACpB,yBAAyB;EACzB,gCAAgC;EAChC,cAAc;AAChB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,mBAAmB;EACnB,0DAA8C;EAC9C,WAAW;EACX,aAAa;EACb,2BAA2B;EAC3B,sBAAsB;AACxB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,iBAAiB;EACjB,kBAAkB;EAClB,UAAU;EACV,YAAY;AACd;;AAEA;EACE,kBAAkB;EAClB,cAAc;EACd,eAAe;EACf,UAAU;AACZ;;AAEA;EACE,cAAc;EACd,gBAAgB;EAChB,eAAe;AACjB;;AAEA;EACE,cAAc;EACd,gBAAgB;EAChB,iBAAiB;AACnB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,WAAW;EACX,eAAe;AACjB;;AAEA;EACE,kBAAkB;EAClB,kBAAkB;AACpB;;AAEA;EACE,gCAAgC;AAClC;;AAEA;EACE,aAAa;EACb,eAAe;EACf,8BAA8B;EAC9B,cAAc;AAChB;;AAEA;EACE,4BAA4B;EAC5B,mBAAmB;EACnB,aAAa;EACb,sBAAsB;AACxB;;AAEA;EACE,yBAAyB;EACzB,8CAA8C;EAC9C,OAAO;AACT;;AAEA;EACE,yBAAyB;EACzB,WAAW;EACX,kBAAkB;EAClB,kBAAkB;EAClB,cAAc;EACd,gBAAgB;EAChB,gBAAgB;AAClB;;AAEA;EACE,WAAW;EACX,iBAAiB;EACjB,iBAAiB;AACnB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,eAAe;EACf,gBAAgB;EAChB,qBAAqB;AACvB;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,cAAc;EACd,eAAe;EACf,gBAAgB;EAChB,oBAAoB;AACtB;;AAEA;EACE,kBAAkB;EAClB,cAAc;EACd,yBAAyB;EACzB,gBAAgB;EAChB,cAAc;AAChB;;AAEA;EACE,qBAAqB;EACrB,cAAc;AAChB;;AAEA;;EAEE,qBAAqB;EACrB,cAAc;AAChB","sourcesContent":["* {\n  box-sizing: border-box;\n  margin: 0;